.card-text {      
    padding-top: 1em;
    padding-left: 1em;
    display: -ms-flexbox;
    display: flex; 
    -ms-flex-direction: row; 
        flex-direction: row;    
    -ms-flex-pack: distribute;    
        justify-content: space-around;
}

.card {  
    padding-bottom:10px;
    font-family: "Roboto", sans-serif;
    border-radius: 1em;
    margin-bottom: 1em;
    background-color: white;
    -webkit-transition: .4s;
    -o-transition: .4s;
    transition: .4s;
}

.card:hover {
    -webkit-transform: scale(1.02);
        -ms-transform: scale(1.02);
            transform: scale(1.02);
    cursor: pointer;
}

.media-container {
    width: 100%;    
    overflow: hidden;
}

.resp-media {    
    top: 0;
    left: 0;
    width: 100%;
    height: auto;   
    border: 0;
}

.detail {
    width: 130px;
}

.info-button {
    -ms-flex-item-align: center;
        align-self: center; 
    margin-right: 1em;
}

.title-small {
    padding-top: 10px;
}
.title-large {
    display:none;    
}
/*hides description on smaller screens */
.short-description {
    display: none;
}



/*formats the card more horizontally on medium size screens */
@media screen and (min-width:767px) {

    .card {
        display: -ms-flexbox;
        display: flex;    
        -ms-flex-pack: center;    
            justify-content: center;
        border: none;
        padding: 0;
    }
    .card-text {
        -ms-flex-direction: column;
            flex-direction: column; 
        -ms-flex-preferred-size: 40%; 
            flex-basis: 40%;    
    }  
    .detail {
        -ms-flex-item-align: center;
            align-self: center;
    }      
    .info-button {
        -ms-flex-item-align: end;
            align-self: flex-end;
        margin-bottom: 1em;
    }
    .title-large {
        display:initial;       
    }
    .title-small {
        display: none;
    }

    .media-container {       
        width: 600px;
        height: auto;
        -ms-flex-preferred-size: auto;
            flex-basis: auto;
        overflow: hidden;
        border-top-left-radius: 15px;
        border-bottom-left-radius: 15px;
    }

    .resp-media {
        width: 100%;
        height: 100%;
    }  
}

/*redistributes white space on large screens */
@media screen and (min-width:1200px) {

    .card-text {
        -ms-flex-preferred-size: 50%;
            flex-basis: 50%;
    }

    .detail {     
        font-size: 18px;  
        display: -ms-flexbox;  
        display: flex;
        width: 100%;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: baseline;
            align-items: baseline;        
        -ms-flex-wrap: nowrap;        
            flex-wrap: nowrap;
    }

    .detail div {
        margin-right: 1em;
    }

    /*displays description on card on large screens */
    .short-description {
        display: initial;
    }   
  



}



